import React from 'react';
import './style.css';
import { Disconnected } from 'pages/Disconnected';
import { Time } from 'pages/Time';
import {
  NavLink as Link,
  BrowserRouter as Router,
  Switch,
  Route,
} from 'react-router-dom';
import Anxious from 'pages/Anxious';

const App = () => {
  return (
    <Router>
      <nav style={{ padding: 20 }}>
        <Link to='/disconnected' style={{ color: 'inherit', margin: 5 }}>
          Disconnected
        </Link>
        <Link to='/time' style={{ color: 'inherit', margin: 5 }}>
          Time
        </Link>
        <Link to='/anxious' style={{ color: 'inherit', margin: 5 }}>
          Anxious
        </Link>
      </nav>

      <Switch>
        <Route path='/disconnected'>
          <Disconnected />
        </Route>
        <Route path='/time'>
          <Time />
        </Route>
        <Route path='/anxious'>
          <Anxious />
        </Route>
      </Switch>
    </Router>
  );
};
export default App;
